@import "global/01-mixins";
@import "fonts";
@import "global/03-vars";
html {
	box-sizing: border-box;

	&::after {
		content: "";
		display: block;
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: rgba(0, 0, 0, 0.7);
		z-index: 50;
		opacity: 0;
		visibility: hidden;
		transition: opacity 0.2s linear, visibility 0.2s linear;
	}

	&._overlay {
		overflow: hidden !important;

		&::after {
			opacity: 1;
			visibility: visible;
		}
	}
}

body {
	font-family: "Montserrat", sans-serif;
	font-size: 20px;
	line-height: 1.25;
	font-weight: 400;
	color: #222;
	position: relative;
	height: 100%;
	background: #f5f8f8;
	-webkit-tap-highlight-color: transparent;
}

div,
section,
footer,
.btn {
	box-sizing: border-box;
}

a {
	color: #000;
	-webkit-transition: all 0.1s linear;
	-moz-transition: all 0.1s linear;
	-ms-transition: all 0.1s linear;
	-o-transition: all 0.1s linear;
	transition: all 0.1s linear;
	text-decoration: none;
}

a:hover {
	text-decoration: none;
}

a:focus,
input:focus {
	outline: none;
}

p {
	color: $colorGrey;
	font-size: 18px;
	font-weight: 500;
	line-height: 1.25;
	margin: 0 0 10px 0;
}

input {
	display: block;
	width: 100%;
	font-size: 14px;
	font-weight: 400;
	line-height: 29px;
	color: #222222;
	height: 57px;
	padding: 20px;
	border-radius: 10px;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
	-webkit-transition: all 0.1s linear;
	-moz-transition: all 0.1s linear;
	-ms-transition: all 0.1s linear;
	-o-transition: all 0.1s linear;
	transition: all 0.1s linear;
	border: none;
	box-shadow: none;
}

.form-agree {
	margin-top: 15px;
	color: $colorWhite;
	text-align: center;
	font-size: 12px;
	font-weight: 400;
}

input:hover,
input:focus {
	outline: none;
}

ol {
	list-style: none;
	padding: 0;
	margin: 0;
	counter-reset: num;
	margin: 30px 0;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	gap: 15px 0;

	li {
		counter-increment: num;
		position: relative;
		padding: 0 0 0 30px;
		font-size: 16px;

		&::before {
			position: absolute;
			top: 0;
			left: 0;
			content: counter(num) ".";
		}
	}
}

ul {
	list-style: none;
	padding: 0;
	margin: 0;
}

ul li {
	position: relative;
	padding: 0 0 0 0;
}

