.top-menu {
	display: flex;
	justify-content: flex-start;
	align-items: center;
	gap: 0 35px;

	@media screen and (max-width: 1023px) {
		display: none;
	}

	li {
		position: relative;
		z-index: 20;

		@media screen and (min-width: 1025px) {
			&:hover > .submenu {
				opacity: 1;
				visibility: visible;
			}

			&:hover > a::before {
				width: 100%;
				transform: scale(1, 1);
				transition: width 0.2s linear;
			}
		}

		.submenu {
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			gap: 10px 0;
			padding: 12px;
			min-width: 280px;
			position: absolute;
			top: 150%;
			left: 0;
			background: $colorWhite;
			box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2);
			opacity: 0;
			visibility: hidden;
			transition: opacity 0.2s linear, visibility 0.2s linear;

			&::before {
				content: "";
				width: 100%;
				position: absolute;
				left: 0;
				top: -12px;
				height: 12px;
			}
		}

		a {
			display: block;
			position: relative;
			width: fit-content;
			font-size: 16px;
			font-weight: 500;
			color: $colorGrey;

			&._active::before {
				width: 100%;
				transform: scale(1, 1);
				transition: width 0.2s linear;
			}

			&::before {
				content: "";
				display: block;
				position: absolute;
				left: 0;
				width: 0;
				bottom: -3px;
				border-bottom: 2px solid $colorBlue;
				transform-origin: right;
				transform: scale(0, 1);
				transition: transform 0.2s ease, width 0s linear 0.2s;
			}

			@media screen and (min-width: 1025px) {
				&:hover::before {
					width: 100%;
					transform: scale(1, 1);
					transition: width 0.2s linear;
				}
			}
		}
	}
}

