.services-item {
	position: relative;
	display: flex;
	flex-direction: column;
	padding: 24px;
	width: calc(50% - 40px * 1 / 2);
	border-radius: 15px;

	@media screen and (max-width: 767px) {
		width: 100%;
	}

	&:first-child {
		.services-item--img {
			top: -15px;
			left: -15px;
		}
	}

	&:nth-child(2) {
		.services-item--img {
			top: -15px;
			right: -20px;
		}
	}

	&:nth-child(3) {
		.services-item--img {
			top: 120px;
			left: -15px;
		}
	}

	&:last-child {
		.services-item--img {
			bottom: -15px;
			right: -15px;
		}
	}

	&:nth-child(odd) {
		align-items: flex-end;

		.services-item--desc {
			text-align: right;

			@media screen and (max-width: 1350px) {
				text-align: left;
			}
		}
	}

	&.blue {
		background: $colorBlue;
	}

	&.green {
		background: $colorGreen;
	}

	&.orange {
		background: $colorOrange;
	}

	&.purple {
		background: $colorPurple;
	}
